import { doc, setDoc } from "firebase/firestore";
import { db } from "./index";

export const setUserToFirebase = async (user:{
    email: string,
    name: string,
    id: string,
    img: string,
}) => {
    await setDoc(doc(db,'userChats', `${user.id}`),{})
    await setDoc(doc(db, "users", `${user.id}`), {
        name: user.name,
        img: user.img,
        email: user.email,
        id: user.id,
      });
}

