import React, { useEffect } from 'react'
import { useDispatch } from 'react-redux'
import { useThemeData } from '../../hook/use-theme'
import { useUserData } from '../../hook/use-user'
import { getUserToFirebase } from '../../store/slice/userSlice'
import AppRoute from '../routes/AppRoute'
import { getLocalStorage } from '../utils/localStorege'
import './app.scss'
const App: React.FC = () => {
  const userStatus = useUserData().status
  const userError = useUserData().error
  const theme = useThemeData()
  const dispatch = useDispatch()
  useEffect(() => {    
    const userId = getLocalStorage('userId')
    userId && dispatch(getUserToFirebase(userId))
  }, [dispatch])
  useEffect(() =>{
    document.documentElement.setAttribute('data-theme',theme.theme)
  },[theme])
  return (
    <div className='app'>
      {userStatus === 'error'
        ?
        <h1> : {userError}</h1>
        :
        userStatus !== 'loading'
        &&
        <AppRoute />
      }
    </div>
  )
}

export default App