import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { getLocalStorage, setLocalStorage } from "../../components/utils/localStorege";

const initialState = getLocalStorage("theme") || { theme: "light", checked: false};

interface setThemeActions {
  theme: string;
  checked: boolean
}

export const themeSlice = createSlice({
  name: "theme",
  initialState,
  reducers: {
    setTheme(state, actions: PayloadAction<setThemeActions>) {
      setLocalStorage('theme', actions.payload)
      state.theme = actions.payload.theme;
      state.checked = actions.payload.checked
    },
  },
});

export const { setTheme } = themeSlice.actions;

export default themeSlice.reducer;
