import React, { useEffect, useState } from 'react'
import { collection, doc, getDoc, getDocs, onSnapshot } from "firebase/firestore";
import { Link, NavLink } from 'react-router-dom'
import { useUserData } from '../../hook/use-user'
import { RouteNames } from '../../route/route'
import MyInput from '../../UI/input/MyInput'
import AsideUserItem from '../asideUserItem'
import './aside.scss'
import { db } from '../../firebase';
import Settings from '../setting';
import SettingSvg from '../../UI/img/SettingSvg';
import { userType } from '../../types/user';
import { createIdChat } from '../../utils/createIdChat';
import { useDispatch } from 'react-redux';
import { setSelectedChat } from '../../store/slice/selectedChatSlice';

type myUsersType = {
  date: object,
  id: string,
  lastMessage: null,
  userInfo: userType
}

const Aside: React.FC = () => {
  const user = useUserData()
  const [value, setValue] = useState<string>('')
  const [users, setUsers] = useState<userType[]>([])
  const [myChats, setMyChats] = useState<myUsersType[] | null>([])
  const [settingShow, setSettingShow] = useState<boolean>(false)
  const dispatch = useDispatch()
  useEffect(() => {
    const app = async () => {
      const allUsers = await getDocs(collection(db, "users"));
      setUsers(allUsers.docs
        .map(user => (
          {
            name: user.data().name,
            id: user.data().id,
            img: user.data().img,
            email: user.data().email,
          }
        )
        ))
    }
    return () => {
      app()
    }
  }, [])
  useEffect(() => {
    const unsub = async () => {
      onSnapshot(doc(db, "userChats", `${user.id}`), (doc) => {
        const users = doc.data()
        if (users && Object.keys(users).length > 0) {
          setMyChats(
            Object.keys(users).map((key: string) => ({
              ...users[key],
              id: key,
            }))
          )
          return
        }
        setMyChats(null)
      });
    }
    return () => {
      unsub()
    }
  }, [])
  const setChat = (user: userType) => {
    user.id && dispatch(setSelectedChat({
      chatId: createIdChat(user.id, user.id),
      selectedUser: user,
    }))
  }
  return (
    <div className='aside'>
      <div className='aside__header'>
        <div className='aside__header-actions'>
          <div className='aside__header-setting'>
            <div className='aside__header-setting-text' onClick={() => setSettingShow(false)}>
              <Link className='aside__header-setting-link' to={RouteNames.Chats}></Link>
            </div>
            <div onClick={() => { setSettingShow(current => !current) }} className='aside__header-setting-img'>
              <SettingSvg />
            </div>
          </div>
          <div className='search'>
            <MyInput classN='search__input' placeholder='  ' type='text' value={value} setValue={setValue} />
          </div>
        </div>
        {settingShow ? <Settings user={user} value={value} /> : <div className='aside__chats'>
          {value && <span className='searct-text'>
               <span className='searct-text__value'>"{value}"</span>
          </span>}
          {value
            ?
            users
              .filter(user =>
                user.name && user.name.toLowerCase().indexOf(value.toLowerCase()) !== -1
              )
              .map(user =>
                <AsideUserItem onClick={() => user.id && setChat(user)} key={user.id} chatId={user.id || ''} name={user.name} img={user.img} id={user.id} />
              )
            :
            myChats ? myChats.map(chat => 
            <AsideUserItem  onClick={() => {}} name={chat.userInfo.name} key={chat.id} img={chat.userInfo.img} id={chat.userInfo.id} chatId={chat.id} lastMessage={chat.lastMessage} />
            )
            : <h3 className='no-chats-text'>    ,      </h3>
          }
        </div>}
      </div>
    </div>
  )
}

export default Aside