import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { userType } from "../../types/user";

interface selectedChatSliceType{
    chatId: string | null
    selectedUser: userType | null
}

const initialState: selectedChatSliceType = {
    chatId: null,
    selectedUser: null,
}


interface setSelectedChatActions {
    chatId: string | null,
    selectedUser: userType | null
}

export const selectedChatSlice = createSlice({
  name: "selectedChat",
  initialState,
  reducers: {
    setSelectedChat(state, actions: PayloadAction<setSelectedChatActions>) {
        state.chatId = actions.payload.chatId
        state.selectedUser = actions.payload.selectedUser
    },
  },
});

export const { setSelectedChat } = selectedChatSlice.actions;

export default selectedChatSlice.reducer;