import React from 'react'
import { Routes } from 'react-router'
import { Navigate, Route } from 'react-router-dom'
import { useUserData } from '../../hook/use-user'
import { privateRoutes, publicRoutes, RouteNames } from '../../route/route'

const AppRoute = () => {
    const auth = useUserData().isAuth
    return auth ?
        (
            <Routes>
                <Route path='/'>
                    {privateRoutes.map(({ path, element, nestedRoute }) =>
                        <Route
                            path={path}
                            key={path}
                            element={element}>

                            {nestedRoute &&
                                <Route
                                    path={nestedRoute.path}
                                    key={nestedRoute.path}
                                    element={nestedRoute.element}
                                />
                            }

                        </Route>)}
                    <Route
                        path="*"
                        element={<Navigate to={RouteNames.Chats} replace />}
                    />
                </Route>
            </Routes>
        )
        :
        (
            <Routes>
                <Route>
                    {publicRoutes.map(({ path, element }) => <Route path={path} element={element} key={path} />)}
                    <Route
                        path="*"
                        element={<Navigate to={RouteNames.Login} replace />}
                    />
                </Route>
            </Routes>
        )
}

export default AppRoute