import { useSelector } from "react-redux"
import { RootState } from "../store"
export const useUserData = () =>{
    const user = useSelector((state: RootState) => state.user)
    return {
        status: user.status,
        error: user.error,
        email: user.email,
        name: user.name,
        id: user.id,
        img: user.img,
        isAuth: !!user.email
    }
}