import {
  doc,
  getDoc,
  serverTimestamp,
  setDoc,
  updateDoc,
} from "firebase/firestore";
import { userType } from "../types/user";
import { db } from "./index";

type ChatDataType = {
  selectedUser: userType | null;
  chatId: string | null;
};

export const setUserChats = async (myUser: userType, chatData: ChatDataType) => {
    if (chatData.chatId && chatData.selectedUser) {
      await setDoc(doc(db, "chats", chatData.chatId), { messages: [] });
      await updateDoc(doc(db, "userChats", `${myUser.id}`), {
        [chatData.chatId + ".userInfo"]: {
          id: chatData.selectedUser.id,
          name: chatData.selectedUser.name,
          email: chatData.selectedUser.email,
          img: chatData.selectedUser.img,
        },
        [chatData.chatId + ".date"]: serverTimestamp(),
        [chatData.chatId + ".lastMessage"]: null,
      });
      await updateDoc(doc(db, "userChats", `${chatData.selectedUser.id}`), {
        [chatData.chatId + ".userInfo"]: {
          id: myUser.id,
          img: myUser.img,
          name: myUser.name,
          email: myUser.email
        },
        [chatData.chatId + ".date"]: serverTimestamp(),
        [chatData.chatId + ".lastMessage"]: null,
      });
    }
};
