import React, { useState } from 'react'
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import { Link } from 'react-router-dom'
import './login.scss'
import { RouteNames } from '../../route/route';
import Button from '../../UI/button';
import MyInput from '../../UI/input';
import { getUserToFirebase } from '../../store/slice/userSlice';
import { useDispatch } from 'react-redux';
import { setLocalStorage } from '../../components/utils/localStorege';
const Login: React.FC = () => {
  const [emailValue, setEmailValue] = useState<string>('')
  const [passwordValue, setPasswordValue] = useState<string>('')
  const dispatch = useDispatch()
  const submit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()
    const auth = getAuth();
    signInWithEmailAndPassword(auth, emailValue, passwordValue)
      .then((userCredential) => {
        const user = userCredential.user;
        setLocalStorage('userId', user.uid)
        dispatch(getUserToFirebase(user.uid))
      })
      .catch((error) => {
        alert(`   ${error.message}`)
      });
  }
  return (
    <div className='login'>
      <div className='login__container'>
        <div className='logo'>messenger</div>
        <div className='form-wrap'>
          <form onSubmit={submit} className='form'>
            <div className='from__text'>  </div>
            <div className='form__element'>
              <MyInput value={emailValue} setValue={setEmailValue} placeholder='  ' type='email' />
            </div>
            <div className='form__element'>
              <MyInput value={passwordValue} setValue={setPasswordValue} placeholder=' ' type='password' />
            </div>
            <div className='form__element'>
              <Button type='submit' text='' classN='form__btn' />
            </div>
            <div className='or'>
              <span className='or__text'></span>
            </div>
            <div className='form__element'>
              <Link to={RouteNames.createAccount}><Button type='button' text='  ' /></Link>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}

export default Login