import { doc, getDoc, onSnapshot, setDoc } from 'firebase/firestore'
import React, { useEffect } from 'react'
import { db } from '../../firebase'
import { setUserChats } from '../../firebase/chat'
import { useSelectedChatData } from '../../hook/use-selectedChat'
import { useUserData } from '../../hook/use-user'
import './chat.scss'
const Chat = () => {
  const myUserData = useUserData()
  const chatData = useSelectedChatData()
  useEffect(() => {
    const app = async () => {
      const docRef = doc(db, 'chats',`${chatData.chatId}`);
      const docSnap = await getDoc(docRef);
      if (docSnap.exists()) {
        console.log("Document data:", docSnap.data());
      } else {
       setUserChats(myUserData,chatData)
      }
    }
    return () => {
      app()
    }
  }, [myUserData,chatData])
  return (
    <div className='chat'>Chat</div>
  )
}

export default Chat

