import React, { useState } from 'react'
import Avatar from 'react-avatar-edit'
import Close from '../../UI/img/Close'
import './avatarEdit.scss'
interface AvatarEditProps {
  setImg: (data: string) => void
  width?: number
  height?: number
  setShow: (data: boolean) => void
}
const AvatarEdit: React.FC<AvatarEditProps> = ({ setImg, width, height,setShow }) => {
  return (
    <div className='avatar'>
      <Avatar
        width={width || 290}
        height={height || 250}
        onCrop={(data: string) => setImg(data)}
      />
      <div onClick={() => setShow(false)} className='close-edit '>
          <Close  />
      </div>
    </div>
  )
}

export default AvatarEdit