import React from 'react'
import { NavLink } from 'react-router-dom'
import { RouteNames } from '../../route/route'
import './AsideUserItem.scss'
interface AsideUserItemProps {
  name: string | null,
  img: string | null,
  lastMessage?: string | null,
  id: string | null,
  chatId: string,
  onClick: (id: string) => void
}
const AsideUserItem:React.FC<AsideUserItemProps> = ({name,img,lastMessage,id,onClick,chatId}) => {
  return (
    <NavLink onClick={() => id && onClick(id)} to={RouteNames.ChatId + chatId} className='aside__chat'>
    <div className='aside__chat-wrap'>
      <div className='aside__chat-img'>
        <img className='aside__chat-img' src={img ? img : 'https://static.thenounproject.com/png/2504016-200.png'} alt="" />
      </div>
      <div className='aside__chat-text'>
        <div className='aside__chat-text-name'>
          {name}
        </div>
        <div className='aside__chat-text-last-message'>
          {lastMessage ? lastMessage : ' '}
        </div>
      </div>
    </div>
  </NavLink>
  )
}

export default AsideUserItem