import React from 'react'
interface MyInputProps {
    value: string
    placeholder: string
    type: string
    classN?: string
    setValue: Function
    minLength?: number
}
const MyInput: React.FC<MyInputProps> = ({value, placeholder, type, setValue, minLength,classN}) => {
    return (
        <input minLength={minLength} value={value} onChange={e => setValue(e.target.value)} placeholder={placeholder} className={`${classN ? classN :  "form__input"}`} type={type} />
    )
}

export default MyInput