import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit";
import { doc, getDoc } from "firebase/firestore";
import { db } from "../../firebase";
import { setUserToFirebase } from "../../firebase/user";
export const getUserToFirebase: any = createAsyncThunk(
  "user/getUserToFirebase",
  async function (id: string, { rejectWithValue }) {
    try {
      const docRef = doc(db, "users", id);
      const docSnap = await getDoc(docRef);    
      return docSnap.data()
    } catch (error) {
      return rejectWithValue(error);
    }
  }
);
export interface userSliceType {
  name: string | null;
  img: string | null;
  email: string | null;
  id: string | null;
  status: string | null;
  error: string | null;
}
const initialState: userSliceType = {
  email: null,
  img: null,
  id: null,
  name: null,
  status: null,
  error: null,
};
interface setUserActions {
  email: string;
  img: string;
  name: string;
  id: string;
  status: string;
  error: string | null;
}
export const userSlice = createSlice({
  name: "user",
  initialState,
  reducers: {
    setUser: (state, actions: PayloadAction<setUserActions>) => {
      setUserToFirebase({ ...actions.payload });
      state.email = actions.payload.email;
      state.img = actions.payload.img;
      state.name = actions.payload.name;
      state.id = actions.payload.id;
      state.error = actions.payload.error;
      state.status = actions.payload.status;
    },
    removeUser: (state) => {
      state.email = null;
      state.img = null;
      state.name = null;
      state.id = null;
      state.error = null;
      state.status = null;
    },
  },
  extraReducers: {
    [getUserToFirebase.pending.toString()]: (
      state,
      actions: PayloadAction<setUserActions>
    ) => {
      state.status = "loading";
      state.error = null;
    },
    [getUserToFirebase.fulfilled.toString()]: (
      state,
      actions: PayloadAction<setUserActions>
    ) => {      
      state.status = "resolved";
      state.error = null; 
      state.name = actions.payload.name;
      state.email = actions.payload.email;
      state.id = actions.payload.id;
      state.img = actions.payload.img;

    },
    [getUserToFirebase.rejected.toString()]: (
      state,
      actions: PayloadAction<setUserActions>
    ) => {    
      state.error = String(actions.payload)
      state.status = "error";
    },
  },
});

export const { setUser } = userSlice.actions;

export default userSlice.reducer;
