import React from 'react'
import './button.scss'
interface ButtonProps {
    disabled?: boolean,
    text: string,
    type: string,
    classN?: string,
    width?: string
}

const Button:React.FC<ButtonProps> = ({disabled,text,type,classN,width}) => {
  return (
    <button style={{
      width: width
    }} disabled={disabled ? true : false} type={type ==='button' ? 'button' : type == 'submit'  ? 'submit' : type ==='reset' ? 'reset' : 'button'} className={`${classN ? classN : 'btn'}`}>{text}</button>
  )
}
export default Button