import React, { useState } from 'react'
import AvatarEdit from '../../components/araterEdit'
import { getAuth, createUserWithEmailAndPassword } from "firebase/auth";
import Button from '../../UI/button'
import MyInput from '../../UI/input'
import './registrations.scss'
import { Link } from 'react-router-dom';
import { RouteNames } from '../../route/route';
import { useDispatch } from 'react-redux';
import { setUser } from '../../store/slice/userSlice';
import { setLocalStorage } from '../../components/utils/localStorege';
const Registrations: React.FC = () => {
  const [nameValue, setNameValue] = useState<string>('')
  const [emailValue, setEmailValue] = useState<string>('')
  const [passwordValue, setPasswordValue] = useState<string>('')
  const [editShow, setEditShow] = useState<boolean>(false)
  const [img, setImg] = useState<string>('https://bipbap.ru/wp-content/uploads/2021/07/1551512888_2-730x617.jpg')
  const dispatch = useDispatch()
  const submit = (e: React.FormEvent) => {
    e.preventDefault()
    const auth = getAuth();
    createUserWithEmailAndPassword(auth, emailValue, passwordValue)
      .then((userCredential) => {
        const user = userCredential.user;
        setLocalStorage('userId', user.uid)
        dispatch(setUser({
          email: emailValue,
          img: img,
          name: nameValue,
          id: user.uid,
          error: null,
          status: "resolved",
        }))
      })
      .catch((error) => {
        error.message === 'Firebase: Error (auth/email-already-in-use).' && alert('     ')
      });
  }
  const editImg = () => {
    setEditShow(true)
  }
  return (
    <div className='registrations'>
      <div className={`${editShow && 'container-registrations-margin'} container-registrations`}>
        <div className='logo'>
          <p className='logo__text'>messenger</p>
        </div>
        <form onSubmit={submit} className='registrations__form'>
          <div className='registrations__form-wrap'>
            <div className='registrations__form-text'>
                
            </div>
            <div className='avatar'>
              {editShow && <div className='avatarEditVrap'><AvatarEdit setImg={setImg} setShow={setEditShow} /></div>}
              <div className='avatar__text'> </div>
              <div onClick={editImg} className='avatar__img-wrap'>
                {!editShow && <img className='avatar__img-edit' src="https://img.icons8.com/plasticine/2x/pencil-tip.png" alt="" />}
                <img className='avatar__img' src={img} alt="" />
              </div>
            </div>
            <MyInput minLength={6} type='text' placeholder=' ' value={nameValue} setValue={setNameValue} />
            <MyInput type='email' placeholder='  ' value={emailValue} setValue={setEmailValue} />
            <MyInput minLength={6} type='password' placeholder=' ' value={passwordValue} setValue={setPasswordValue} />
            <div className='avatar__btn'>
              <Button width='210px' type='submit' text='' />
            </div>
            <div className='or'>
              <span className='or__text'></span>
            </div>
            <Link className='link__back' to={RouteNames.Login}>  </Link>
          </div>
        </form>
      </div>
    </div>
  )
}

export default Registrations