import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import { useThemeData } from '../../hook/use-theme'
import { setTheme } from '../../store/slice/themeSlice'
import { userType } from '../../types/user'
import Button from '../../UI/button'
import './settings.scss'
interface SettingsProps {
  value: string,
  user: userType
}

const Settings: React.FC<SettingsProps> = ({ value, user }) => { 
  const theme = useThemeData()
  const dispatch = useDispatch()
  const toggleThemne = (checked: boolean) => {
    checked === true ? dispatch(setTheme({theme: 'dark',checked: true})) : dispatch(setTheme({theme: 'light',checked: false}));
  }
  return (
    <div className='settings'>
      <div className='setting'>
        <div className='theme'>
          <div className='theme__switch'>
            <h4 className='setting__text'> </h4>
            <label className="switch">
              <input type="checkbox" checked={theme.checked} onChange={(e: React.ChangeEvent<HTMLInputElement>) => toggleThemne(e.target.checked)} />
              <span className="slider round"></span>
            </label>
          </div>
        </div>
        {/* <div className='language'>
          <div className='setting__text'>
          <h4 className='language__text'> </h4>
          <label id="switch" className="switch">
            <input type="checkbox" onChange={toggleThemne} />
            <span className="slider round"></span>
          </label>
          </div>
        </div> */}
      </div>
    </div>
  )
}

export default Settings